.apology-section {
	position: relative;
	padding: 5rem 0;
	background-color: var(--color-primary);
	border-radius: var(--border-radius);
	box-shadow: inset 0 8px 1rem rgba($color: #000000, $alpha: 0.15);
	@include max-md {
		padding: 3rem 0;
	}
	.appology-wrap {
		position: relative;
	}
}
