.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			&::after {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				border-radius: 300px 300px 20px 20px;
				box-shadow: inset 0 10px 1rem var(--color-main);
				content: "";
			}
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 500px 500px 20px 20px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			border-radius: 50%;
			background: radial-gradient(ellipse, rgba(255, 255, 255, 0.6) 0%, transparent 80%);
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
